import React, { useEffect, useRef, useState } from 'react';
import { Box, Input, Button, Grid, Card } from '@material-ui/core';
import { Area, AreaChart, CartesianGrid, ResponsiveContainer, Tooltip, XAxis, YAxis } from 'recharts';
import { makeStyles, useTheme } from '@material-ui/styles';
import { ArrowDropUp, ArrowDropDown } from '@material-ui/icons';
import { MinimizeIcon } from './icons/icon';
import MaxSwapDialog from '../MaxSwapDialog';
import { ETH_ADDRESS, WETH_ADDRESS } from 'utils/constants';
import util from 'API-Service/util';
import moment from 'moment';
import { getBlockTimestamp, getCorrectDecValue } from 'web3/contracts';
import { CustomizedActiveDot } from 'components/Chart';

const useStyles = makeStyles(theme => ({
  card: {
    padding: 32,
    borderRadius: '25px',
    border: `1px solid ${theme.palette.border_color}`,
    boxShadow: 'none'
  },
  header: {
    fontSize: 20,
    lineHeight: '28px',
    color: theme.palette.text_main,
    display: 'flex', 
    alignItems: 'center', 
    justifyContent: 'space-between',
  },
  change_plus: {
    color: '#108F65',
    display: 'flex',
    marginTop: 8,
    marginBottom: 16
  },
  change_minus: {
    color: '#FF8585',
    display: 'flex',
    marginTop: 8,
    marginBottom: 16
  },
  change_normal: {
    color: theme.palette.text_sub,
    display: 'flex',
    marginTop: 8,
    marginBottom: 16
  },
  price: {
    marginRight: theme.spacing(2)
  },
  timeBox: {
    color: theme.palette.blue,
    marginTop: 16,
    justifyContent: 'flex-end',
    display: 'flex',
  },
  tooltipCard: {
    background: theme.palette.black_white,
    border: `1px solid ${theme.palette.border_color}`,
    boxShadow: '0px 4px 28px rgba(63, 66, 113, 0.15)',
    borderRadius: 16,
    padding: 24,
    color: theme.palette.text_main,
    fontSize: 20,
    lineHeight: '28px'
  },
  priceContainer: {
    color: theme.palette.text_main,
    fontWeight: 700,
    display: 'flex',
    alignItems: 'center'
  },
  dot: {
    width: 8,
    height: 8,
    borderRadius: 4,
    backgroundColor: '#1DB981',
    marginRight: 8
  },
  focusIcon: {
    '&.MuiSvgIcon-root': {
      fontSize: `1.2rem`
    }
  }
}));

const timeKey = [
  {title: '1 hour', time: 3600},
  {title: '1 day', time: 3600 * 24},
  {title: '1 week', time: 3600 * 24 * 7},
  {title: '1 month', time: 3600 * 24 * 30},
  {title: '1 year', time: 3600 * 24 * 365},
  {title: 'all', time: 3600 * 24 * 365 * 10}

]
const SwapChart = props => {
  const { fromToken, toToken } = props;
  const theme = useTheme();
  const classes = useStyles(theme);
  const [data, setData] = useState([]);
  const [currentPrice, setCurrentPrice] = useState(0);
  const [changePrice, setChangePrice] = useState(0);
  const [change, setChange] = useState(0);
  const [minPrice, setMinPrice] = useState(0);
  const [maxPrice, setMaxPrice] = useState(0);
  const [timeIndex, setTimeIndex] = useState(0);
  const [open, setOpen] = useState(false);

  useEffect(() => {
    if (data.length > 0) {
      let min = 10000000000;
      let max = 0;
      setChangePrice(
        getCorrectDecValue(
        (data[data.length - 1].quotePrice - data[data.length - 2].quotePrice), 0, 2)
      );
      setChange(
        getCorrectDecValue(
        (data[data.length - 1].quotePrice / data[data.length - 2].quotePrice - 1), 0, 2)
      );
      setCurrentPrice(getCorrectDecValue(data[data.length - 1].quotePrice, 0, 2));
      for (let i = 0; i < data.length; i++) {
        if (min > data[i].quotePrice) min = data[i].quotePrice;
        if (max < data[i].quotePrice) max = data[i].quotePrice;
      }
      setMinPrice(min - (max - min) / 2);
      setMaxPrice(max + (max - min) / 2);
    }
  }, [data]);

  useEffect(() => {
    if (fromToken.address && toToken.address) {
      getSwapChartInfo(
        fromToken.address == ETH_ADDRESS ? WETH_ADDRESS : fromToken.address, 
        toToken.address == ETH_ADDRESS ? WETH_ADDRESS : toToken.address);
    }
  }, [fromToken.address, toToken.address, timeIndex])

  const getSwapChartInfo = async (fromAddress, toAddress) => {
    let blockTimestamp = await getBlockTimestamp();
    let res = await util.getSwapChart(fromAddress, toAddress, blockTimestamp - timeKey[timeIndex].time)
    setData(res.data.tokenPricesData[0].prices.sort((a,b) => a.transactionTime - b.transactionTime))
  }
  const handleClose = () => {
    setOpen(false);
  } 

  const renderTooltip = (variable) => {
    let index = variable?.label;

    let payload;
    if (variable.payload && variable.payload.length > 0) {
      payload = variable.payload[0].payload
      setCurrentPrice(getCorrectDecValue(payload.quotePrice, 0, 2));
      if (index && index > 0) {
        setChangePrice(
          getCorrectDecValue(
          (data[index].quotePrice - data[index -1].quotePrice), 0, 2)
        );
        setChange(
          ((data[index].quotePrice || 1) / (data[index - 1].quotePrice || 1) - 1).toFixed(
            3
          )
        );
      }
    }
    else {
      payload = {}
      if (data.length > 0) {
        setCurrentPrice(getCorrectDecValue(data[data.length - 1].quotePrice, 0, 2))
        if (index && index > 0) {
          setChangePrice(
            getCorrectDecValue(
            (data[data.length - 1].quotePrice - data[data.length - 2].quotePrice), 0, 2)
          );
          setChange(
            getCorrectDecValue(
            ((data[data.length - 1].quotePrice || 1) / (data[data.length - 2].quotePrice || 1) - 1), 0, 2
            )
          );
        }
      }
    }
    return (
      <Card className={classes.tooltipCard}>
        <Box style={{marginBottom: 6}}>
          {moment(new Date(payload.transactionTime * 1000)).format("DD/MM/YYYY HH:mm")}
        </Box>
        <Box className={classes.priceContainer}>
          <Box className={classes.dot}/>
          <Box>
            {payload.quotePrice}
          </Box>
        </Box>
      </Card>
    )
  }

  return (
    <Card className={classes.card}>
      <Box className={classes.header}>
        <Box className={classes.current_price}>${currentPrice}</Box>
        <MinimizeIcon style={{cursor: 'pointer'}} onClick={() => setOpen(true)}/> 
      </Box>
      <Box
        className={
          changePrice > 0
            ? classes.change_plus
            : changePrice < 0
            ? classes.change_minus
            : classes.change_normal
        }>
        <Box className={classes.price}>
          {changePrice > 0 ? `+${changePrice}` : changePrice}
        </Box>
        <Box style={{ display: 'flex' }}>
          {changePrice > 0 ? (
            <ArrowDropUp className={classes.focusIcon}/>
          ) : changePrice < 0 ? (
            <ArrowDropDown className={classes.focusIcon}/>
          ) : (
            <></>
          )}
          <Box>{Math.abs(change)}%</Box>
        </Box>
      </Box>
      <div style={{ height: 300 }}>
        <ResponsiveContainer>
          <AreaChart
            height={400}
            data={data}
            margin={{
              left: -25,
              bottom: 0
            }}>
            <defs>
            <linearGradient id="colorUv" x1="0" y1="0" x2="0" y2="1">
              <stop offset="0%" stopColor="#E0E3FF" stopOpacity={1} />
              <stop offset="100%" stopColor="#E0E3FF" stopOpacity={0} />
            </linearGradient>
            </defs>
            <CartesianGrid vertical={false} strokeDasharray="4"/>
            <XAxis hide />
            <YAxis
              hide
              domain={[minPrice, maxPrice]}
            />
            <Tooltip content={renderTooltip}/>
            <Area
              type="monotone"
              dataKey="quotePrice"
              stroke="#B2B7FF"
              fill="url(#colorUv)"
              activeDot={<CustomizedActiveDot />}
            />
          </AreaChart>
        </ResponsiveContainer>
      </div>
      <Box className={classes.timeBox}>
        {timeKey[timeIndex].title}
      </Box>
      <MaxSwapDialog
        open={open}
        handleClose={handleClose}
        timeIndex={timeIndex}
        onSelectTime={(time) => setTimeIndex(time)}
        fromToken={fromToken}
        toToken={toToken}
      />
    </Card>
  );
};

export default SwapChart;
