import { Box, Button, Dialog, IconButton, Popover, Card, List, ListItem } from '@material-ui/core';
import React, { useEffect, useState } from 'react';
import { Area, AreaChart, CartesianGrid, ResponsiveContainer, Tooltip, XAxis, YAxis } from 'recharts';
import { CloseIcon } from './icons/icon';
import useStyles from './style';
import { DownIcon, UpIcon } from 'components/ExpandCard/icons/icons';
import { ETH_ADDRESS, WETH_ADDRESS } from 'utils/constants';
import util from 'API-Service/util';
import moment from 'moment';
import { getBlockTimestamp } from 'web3/contracts';
import { ArrowDropDown, ArrowDropUp } from '@material-ui/icons';
import { CustomizedActiveDot } from 'components/Chart';

const timeKey = [
  {title: '1H', time: 3600},
  {title: '1D', time: 3600 * 24},
  {title: '1W', time: 3600 * 24 * 7},
  {title: '1M', time: 3600 * 24 * 30},
  {title: '1Y', time: 3600 * 24 * 365},
  {title: 'ALL', time: 3600 * 24 * 365 * 10}

]

const MaxSwapDialog = props => {
  const { open, handleClose, onSelectTime, timeIndex, fromToken, toToken } = props;
  const [data, setData] = useState([]);
  const [currentPrice, setCurrentPrice] = useState(0);
  const [changePrice, setChangePrice] = useState(0);
  const [change, setChange] = useState(0);
  const [minPrice, setMinPrice] = useState(0);
  const [maxPrice, setMaxPrice] = useState(0);
  const classes = useStyles();

  useEffect(() => {
    if (data.length > 1) {
      let min = 10000000000;
      let max = 0;
      setChangePrice(
        (data[data.length - 1].quotePrice - data[data.length - 2].quotePrice).toFixed(3)
      );
      setChange(
        (data[data.length - 1].quotePrice / data[data.length - 2].quotePrice - 1).toFixed(
          3
        )
      );
      setCurrentPrice(data[data.length - 1].quotePrice.toFixed(3));
      for (let i = 0; i < data.length; i++) {
        if (min > data[i].quotePrice) min = data[i].quotePrice;
        if (max < data[i].quotePrice) max = data[i].quotePrice;
      }
      setMinPrice(min - (max - min) / 2);
      setMaxPrice(max + (max - min) / 2);
    }
  }, [data]);

  useEffect(() => {
    if (fromToken.address && toToken.address) {
      getSwapChartInfo(
        fromToken.address == ETH_ADDRESS ? WETH_ADDRESS : fromToken.address, 
        toToken.address == ETH_ADDRESS ? WETH_ADDRESS : toToken.address);
    }
  }, [fromToken.address, toToken.address, timeIndex])

  const getSwapChartInfo = async (fromAddress, toAddress) => {
    let blockTimestamp = await getBlockTimestamp();
    let res = await util.getSwapChart(fromAddress, toAddress, blockTimestamp - timeKey[timeIndex].time)
    setData(res.data.tokenPricesData[0].prices.sort((a,b) => a.transactionTime - b.transactionTime))
  }

  const renderTooltip = (variable) => {
    let index = variable?.label;

    let payload;
    if (variable.payload && variable.payload.length > 0) {
      payload = variable.payload[0].payload
      setCurrentPrice(payload.quotePrice.toFixed(3));
      if (index && index > 0) {
        setChangePrice(
          (data[index].quotePrice - data[index -1].quotePrice).toFixed(2)
        );
        setChange(
          ((data[index].quotePrice || 1) / (data[index - 1].quotePrice || 1) - 1).toFixed(
            3
          )
        );
      }
    }
    else {
      payload = {}
      if (data.length > 0) {
        setCurrentPrice(data[data.length - 1].quotePrice.toFixed(3))
        if (index && index > 0) {
          setChangePrice(
            (data[data.length - 1].quotePrice - data[data.length - 2].quotePrice).toFixed(2)
          );
          setChange(
            ((data[data.length - 1].quotePrice || 1) / (data[data.length - 2].quotePrice || 1) - 1).toFixed(
              3
            )
          );
        }
      }
    }
    return (
      <Card className={classes.tooltipCard}>
        <Box style={{marginBottom: 6}}>
          {moment(new Date(payload.transactionTime * 1000)).format("DD/MM/YYYY HH:mm")}
        </Box>
        <Box className={classes.priceContainer}>
          <Box className={classes.dot}/>
          <Box>
            {payload.quotePrice}
          </Box>
        </Box>
      </Card>
    )
  }

  return (
    <Dialog onClose={handleClose} open={open} className={classes.dialog}>
      <Box className={classes.header}>
        <Box className={classes.current_price}>${currentPrice}</Box>
        <Box className={classes.icon} onClick={handleClose}>
          <IconButton
            color="primary"
            aria-label="upload picture"
            component="span"
            className={classes.control_button}>
            <CloseIcon />
          </IconButton>
        </Box>
      </Box>
      <Box
        className={
          changePrice > 0
            ? classes.change_plus
            : changePrice < 0
            ? classes.change_minus
            : classes.change_normal
        }>
        <Box className={classes.price}>
          {changePrice > 0 ? `+${changePrice}` : changePrice}
        </Box>
        <Box style={{ display: 'flex'}}>
          {changePrice > 0 ? (
            <ArrowDropUp className={classes.focusIcon}/>
          ) : changePrice < 0 ? (
            <ArrowDropDown className={classes.focusIcon}/>
          ) : (
            <></>
          )}
          <Box>{Math.abs(change)}%</Box>
        </Box>
      </Box>
      <div style={{ height: 300 }}>
        <ResponsiveContainer>
          <AreaChart
            height={400}
            data={data}
            margin={{
              left: -25,
              bottom: 0
            }}>
            <defs>
              <linearGradient id="colorUv" x1="0" y1="0" x2="0" y2="1">
                <stop offset="0%" stopColor="#E0E3FF" stopOpacity={1} />
                <stop offset="100%" stopColor="#E0E3FF" stopOpacity={0} />
              </linearGradient>
            </defs>
            <CartesianGrid vertical={false} strokeDasharray="4"/>
            <XAxis hide />
            <YAxis
              hide
              domain={[minPrice, maxPrice]}
            />
            <Tooltip content={renderTooltip}/>
            <Area
              type="monotone"
              dataKey="quotePrice"
              stroke="#B2B7FF"
              fill="url(#colorUv)"
              activeDot={<CustomizedActiveDot />}
            />
          </AreaChart>
        </ResponsiveContainer>
      </div>
      <List
        className="tableScrollBar"
        style={{ display: 'flex', overflowX: 'auto', justifyContent: 'space-between', marginTop: 36 }}>
        {timeKey.map((item, index) => (
          <ListItem
            key={item.time}
            selected={timeIndex === index}
            className={classes.button}
            onClick={() => onSelectTime(index)}>
            {item.title}
          </ListItem>
        ))}
      </List>
    </Dialog>
  );
};

export default MaxSwapDialog;
