import React, { useEffect, useRef, useState } from 'react';
import { makeStyles } from '@material-ui/styles';
import { Box, Input, Button, Grid } from '@material-ui/core';
import { KeyboardArrowDown } from '@material-ui/icons';
import { TokenSelectModal } from 'components';
import { getBNValue, getCorrectDecValue } from 'web3/contracts';
import { UNISWAP_ADDRESS, USDT_ADDRESS, USDC_ADDRESS, WETH_ADDRESS, ETH_ADDRESS } from 'utils/constants';
import { getAmountsOut } from 'web3/uniswap';
import { LeftIcon, SelectIcon } from 'views/Swap/icons/icon';

const useStyles = makeStyles(theme => ({
  error_root: {
    border: `1px solid ${theme.palette.blue_light_border_color}`,
    borderRadius: 12,
    padding: 16,
    backgroundColor: theme.palette.black_white,
  },
  root: {
    border: `1px solid ${theme.palette.blue_light_border_color}`,
    borderRadius: 12,
    padding: 16,
    backgroundColor: theme.palette.black_white,
    [theme.breakpoints.down('xs')]: {
      padding: 3,
    },
  },
  container: {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center'
  },
  selected_input: {
    flexGrow: 1,
    fontSize: '24px',
    lineHeight: '16px',
    letterSpacing: '-0.05px',
    color: theme.palette.text_main,
    width: '150px',
    '&::placeholder': {
      color: `${theme.palette.text_main} !important`
    },
    '&.MuiInputBase-root.Mui-disabled': {
      color: `${theme.palette.text_main} !important`
    }
  },
  input: {
    flexGrow: 1,
    fontSize: '24px',
    lineHeight: '16px',
    letterSpacing: '-0.05px',
    color: theme.palette.text_main,
    width: '150px',
    '&::placeholder': {
      color: `${theme.palette.text_disable} !important`
    },
    '&.MuiInputBase-root.Mui-disabled': {
      color: `${theme.palette.text_disable} !important`
    }
  },
  tag: {
    color: theme.palette.text_sub,
    fontSize: 14,
    marginRight: theme.spacing(1)
  },
  tokenContainer: {
    display: 'flex',
    alignItems: 'center',
  },
  non_selected_connect_button: {
    boxShadow: 'none',
    borderRadius: 9,
    border: `1px solid ${theme.palette.border_color}`,
    color: theme.palette.text_main,
    backgroundColor: theme.palette.wallet_button_color,
    textTransform: 'none',
    padding: 10,
    display: 'flex',
    justifyContent: 'space-between',
    '&:hover': {
      backgroundColor: theme.palette.wallet_button_color,
      boxShadow: 'none'
    },
    [theme.breakpoints.down('xs')]: {
      marginTop: "1rem",
    },
  },
  connect_button: {
    boxShadow: 'none',
    borderRadius: 9,
    border: `1px solid ${theme.palette.border_color}`,
    color: theme.palette.text_main,
    backgroundColor: theme.palette.wallet_button_color,
    textTransform: 'none',
    display: 'flex',
    justifyContent: 'space-between',
    padding: '0px 10px 0px 0px',
    width: 157,
    '&:hover': {
      backgroundColor: theme.palette.wallet_button_color,
      boxShadow: 'none'
    }
  },
  imageBox: {
    width: 40,
    height: 40,
    marginRight: 10,
    backgroundColor: theme.palette.wallet_button_color,
    borderRadius: 9,
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center'
  },
  image: {
    width: 20,
    height: 20,
  },
  title: {
  },
  icon: {
    marginRight: 6.5,
    marginLeft: 16.5,
    display: 'flex',
    alignItems: 'center'
  },
  valueContainer: {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
    marginTop: 8
  },
  mainText: {
    fontSize: 14,
    color: theme.palette.text_main
  },
  subText: {
    fontSize: 14,
    color: theme.palette.text_sub
  },
  disableText: {
    fontSize: 14,
    color: theme.palette.text_disable
  },
  error: {
    fontSize: 14,
    color: '#FF8585',
  }
}));

const TokenInput = props => {
  const { children, tokenInfo, handleChange, handleChangeAmount, isFrom } = props;
  const classes = useStyles();
  const [open, setOpen] = useState(false);

  const getAmountOut = async (token) => {
    let toAddress = (token?.address == ETH_ADDRESS ? WETH_ADDRESS : token?.address);
    let res = 0;
    const result = await getAmountsOut(UNISWAP_ADDRESS, [toAddress, toAddress === USDT_ADDRESS ? USDC_ADDRESS : USDT_ADDRESS], getBNValue(1, token.decimals) )
    if (result.status) {
      res = getCorrectDecValue(result.data[1], 6, 2, false)
    }
    return res;
  }

  const onChangeInput = (e) => {
    let _tokenInfo = JSON.parse(JSON.stringify(tokenInfo));
    _tokenInfo.amount = e.target.value;
    handleChangeAmount(_tokenInfo);
  }

  const onChangeToken = async (item) => {
    let _tokenInfo = item;
    _tokenInfo.price = await getAmountOut(_tokenInfo)
    handleChange(_tokenInfo);
  }

  return (
    <Box className={(isFrom && tokenInfo?.symbol && ((tokenInfo?.amount || 0) > (tokenInfo?.balance || 0))) ?
      classes.error_root : classes.root}>
      <Box className={classes.container}>
        <Box>
          <Input
            className={tokenInfo?.symbol ? classes.selected_input : classes.input}
            disableUnderline
            disabled={!isFrom}
            onChange={onChangeInput}
            placeholder={"0"}
            value={tokenInfo?.amount || ''}
          />
        </Box>
        <Box>
          <Box className={classes.tokenContainer} onClick={() => setOpen(true)}>
            <Button variant="contained" className={tokenInfo?.symbol ? classes.connect_button : classes.non_selected_connect_button}>
              {tokenInfo?.symbol ? (
                <>
                  <Box className={classes.imageBox}>
                    <img src={tokenInfo?.logoURI} className={classes.image} />
                  </Box>
                  <Box className={classes.title}>{tokenInfo?.symbol}</Box>
                </>
              ) : (
                <>
                  <SelectIcon style={{marginRight: 6}}/>
                  <Box className={classes.subText}>Select token</Box>
                </>
              )}
              <Box className={classes.icon}>
                <LeftIcon />
              </Box>
            </Button>
          </Box>
        </Box>
      </Box>
      <Box className={classes.valueContainer}>
        {
          (isFrom && tokenInfo?.symbol && ((tokenInfo?.amount || 0) > (getCorrectDecValue(tokenInfo?.balance || 0, tokenInfo?.decimals || 0, -1, false)))) ?
          <Box className={classes.error}>
            {`Insufficient ${tokenInfo?.symbol} balance`}
          </Box>
          :
          <Box className={tokenInfo?.symbol ? classes.mainText : classes.disableText}>
            { tokenInfo?.symbol ? `$${getCorrectDecValue((tokenInfo?.price || 0) * (tokenInfo?.amount || 0), 0, 4)}` : '0 (+/- 0%)' }
          </Box>
        }
        <Box style={{display: 'flex'}}>
          <Box className={classes.subText} style={{marginRight: 4}}>
            Token balance:
          </Box>
          <Box className={classes.mainText}>
            {getCorrectDecValue(tokenInfo?.balance || 0, tokenInfo?.decimals || 1, 2)}
          </Box>
        </Box>
      </Box>
      <TokenSelectModal
        open={open}
        handleClose={() => setOpen(false)}
        handleSelectItem={(item) => onChangeToken(item)}
      />
    </Box>
  );
};

export default TokenInput;
