import React, { useState, useEffect, useMemo } from 'react';
import { makeStyles, useTheme } from '@material-ui/styles';
import { Grid, Card, Box, Button, CircularProgress } from '@material-ui/core';
import useStyles from './style';
import { NetworkSelector, TransactionSettingsModal, StepViewer, LoadingModal } from 'components';
import { SwapChart, TokenInput } from './components';
import { LoadingIcon, PreviewIcon, RevertIcon, SettingIcon } from './icons/icon';
import useActiveWeb3React from "hooks/web3-react"
import Web3 from 'web3';
import { approveToken, gasPrice, getBNValue, getCorrectDecValue, getErc20Balance, getGasSwapToken, getLiquidityBalance, swapToken } from '../../web3/contracts';
import { CLUTCH_DEFI_ADDRESS, ETH_ADDRESS, MAIN_NETWORK_ID, NETWORK_TX_ENDPOINT, UNISWAP_ADDRESS, USDT_ADDRESS, WETH_ADDRESS } from 'utils/constants';
import { getAmountsOut } from 'web3/uniswap';
import { switchNetwork } from 'web3/network';
import { useToasts } from 'react-toast-notifications';
import { useSelector } from 'react-redux';
import { BackIcon } from 'views/Send/icons/icon';
import util from 'API-Service/util';

const getPriceImpact = (fromBalance, toBalance, _fromToken, _toToken) => {
  let toTokenRemainAmount = (_toToken.amount - (fromBalance * toBalance / (Number(fromBalance) + Number(_fromToken?.amount) || 0)))
  let rate = _fromToken.amount / toTokenRemainAmount
  return (rate / (fromBalance / toBalance) - 1).toFixed(3);
}

const Swap = props => {
  const theme = useTheme();
  const classes = useStyles(theme);
  const networkList = [
    { networkName: 'Ethereum', image: '/images/EthereumLogo.png' },
    { networkName: 'Polygon', image: '/images/Polygon.png' },
    { networkName: 'Arbitrum', image: '/images/ArbitrumLogo.png' }
  ];
  const [selectedNetwork, setSelectedNetwork] = useState({
    networkName: 'Ethereum',
    image: '/images/EthereumLogo.png'
  });
  const [fromToken, setFromToken] = useState({});
  const [settingsModalOpen, setSettingsModalOpen] = useState(false);
  const [toToken, setToToken] = useState({});
  const [slippage, setSlippage] = useState(0);
  const [speed, setSpeed] = useState(0);
  const [inputedSlippage, setInputedSlippage] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(false);
  const [ethPrice, setEthPrice] = useState(0);
  const [gas, setGas] = useState(0);
  const [gasLimit, setGasLimit] = useState(21000);
  const [hash, setHash] = useState('');
  const [outputMin, setOutputMin] = useState(0);
  const [approveButton, setApproveButton] = useState(false);
  const [totalFromBalance, setTotalFromBalance] = useState(0);
  const [totalToBalance, setTotalToBalance] = useState(0);
  const [step, setStep] = useState(0);
  const [approveLoading, setApproveLoading] = useState(false);
  const [data, setData] = useState([]);
  const priceImpact = useMemo(() => getPriceImpact(totalFromBalance,  totalToBalance, fromToken, toToken), [totalFromBalance,  totalToBalance, fromToken, toToken]);
  const { account, library, chainId } = useActiveWeb3React();
  const currentWallet = useSelector(state => state.app.currentWallet);
  const { addToast } = useToasts();

  useEffect(() => {
    if (selectedNetwork?.networkName != 'Ethereum') {
      addToast('Clutch is only supporting Ethereum network. Please switch to the Ethereum network', { appearance: 'error', autoDismissTimeout: 5000, autoDismiss: true })
    }
  }, [selectedNetwork])
  useEffect(() => {
    getEthPrice()
  }, [])

  useEffect(() => {
    getGasPrice()
  }, [speed])

  useEffect(() => {
    if (step == 1) {
      estimateGas()
    }
  }, [step])

  useEffect(() => {
    let _slippage = 0;
    if (inputedSlippage > 0) {
      _slippage = Number(inputedSlippage)
    } else if (slippage == 0) {
      _slippage = 0.5;
    } else if (slippage == 1) {
      _slippage = 1;
    } else if (slippage == 2) {
      _slippage = 2;
    }
    setOutputMin((100 - _slippage) * (getBNValue(toToken.amount || 0, toToken.decimals)))
  }, [slippage, inputedSlippage, toToken])

  useEffect(() => {
    if (fromToken.address && fromToken.address != ETH_ADDRESS)
      setApproveButton(true)
    else
      setApproveButton(false)
  }, [fromToken])

  useEffect(() => {
    const getTotalBalance = async () => {
      const ret = await getLiquidityBalance(fromToken.address, toToken.address)
      if (ret.status) {
        setTotalFromBalance(getCorrectDecValue(ret.data.from, fromToken.decimals, -1, false));
        setTotalToBalance(getCorrectDecValue(ret.data.to, toToken.decimals, -1, false));
      }
    }
    if (fromToken.symbol && toToken.symbol) {
      getTotalBalance()
    }
  }, [fromToken, toToken])

  const getEthPrice = async () => {
    let res = 0;
    const result = await getAmountsOut(UNISWAP_ADDRESS, [WETH_ADDRESS, USDT_ADDRESS], getBNValue(1, 18) )
    if (result.status) {
      res = getCorrectDecValue(result.data[1], 6, -1, false)
    }
    setEthPrice(res)
  }

  const getGasPrice = async () => {
    let res = await gasPrice();
    if (speed == 0)
      setGas(res)
    else if (speed == 1)
      setGas(res * 1.5)
    else
      setGas(res * 2)
  }

  const estimateGas = async () => {
    const res = await getGasSwapToken(fromToken?.address, toToken?.address, currentWallet, getBNValue(fromToken?.amount, fromToken?.decimals), getBNValue(toToken?.amount, toToken?.decimals), outputMin)
    if (res.status) {
      setGasLimit(res.data)
    }
  }


  const getAmountOut = async (_fromToken, _toToken) => {
    let fromAddress = (_fromToken?.address == ETH_ADDRESS ? WETH_ADDRESS : _fromToken?.address);
    let toAddress = (_toToken?.address == ETH_ADDRESS ? WETH_ADDRESS : _toToken?.address);
    let amount = 0;
    const result = await getAmountsOut(UNISWAP_ADDRESS, [fromAddress, toAddress], getBNValue(_fromToken?.amount, _fromToken.decimals) )
    if (result.status) {
      amount = getCorrectDecValue(result.data[1], _toToken.decimals, -1, false)
    }
    return amount;
  }

  const handleChangeToken = async (item, isFrom) => {
    if (isFrom) {
      setFromToken(item)
    } else {
      setToToken(item)
    }
  }

  const handleChangeAmount = async (item) => {
    setFromToken(item)
    if (fromToken?.symbol && toToken?.symbol) {
      let _toToken = JSON.parse(JSON.stringify(toToken));
      if (!isNaN(item.amount) && Number(item.amount) != 0) {
        _toToken.amount = await getAmountOut(item, toToken);
      } else {
        _toToken.amount = 0;
      }
      setToToken(_toToken)
    }
  }

  useEffect(() => {
    const setAmount = async () => {
      if (fromToken.symbol && toToken.symbol) {
        let _toToken = JSON.parse(JSON.stringify(toToken));
        if (!isNaN(fromToken.amount) && Number(fromToken.amount) != 0) {
          _toToken.amount = await getAmountOut(fromToken, toToken);
        } else {
          _toToken.amount = 0;
        }
        setToToken(_toToken)
      }
    }
    setAmount()
  }, [fromToken.symbol, toToken.symbol])

  const exchangeToken = () => {
    let _fromToken = JSON.parse(JSON.stringify(fromToken));
    let _toToken = JSON.parse(JSON.stringify(toToken));
    setFromToken(_toToken);
    setToToken(_fromToken);
  }

  const handleApply = (_slippage, _speed, _inputedSlippage) => {
    setSlippage(_slippage);
    setSpeed(_speed);
    setInputedSlippage(_inputedSlippage)
  }

  const previewSwap = async () => {
    if (!fromToken?.symbol || !toToken?.symbol || !fromToken?.amount || fromToken?.amount == 0 || fromToken?.amount >= getCorrectDecValue(fromToken?.balance || 0, fromToken?.decimals, -1, false))
      addToast('Invalid token, amount', { appearance: 'error', autoDismiss: true });
    else {
      setStep(1);
    }
  }
  const swap = async () => {
    if (!currentWallet || currentWallet.length == 0)
      addToast('Please connect wallet.', { appearance: 'error', autoDismiss: true});
    else if (currentWallet != account?.toLowerCase())
      addToast(`Please switch your account to ${currentWallet}`, { appearance: 'error', autoDismiss: true});
    else if (library) {
      setStep(2);
      setLoading(true);
      setError(false);
      const net_ret = await switchNetwork(MAIN_NETWORK_ID)
      if (net_ret) {
        const web3 = new Web3(library.provider)
        const result = await swapToken(web3, fromToken?.address, toToken?.address, currentWallet, getBNValue(fromToken?.amount, fromToken?.decimals), getBNValue(toToken?.amount, toToken?.decimals), outputMin, gas, gasLimit)
        if (result.status) {
          setError(false);
          setHash(result.data.hash)
        } else {
          setError(true);
        }
      } else {
        setError(true);
      }

      setLoading(false);
    }
  }

  const handleApprove = async () => {
    if (!currentWallet || currentWallet.length == 0)
      addToast('Please connect wallet.', { appearance: 'error', autoDismiss: true});
    else if (currentWallet != account?.toLowerCase())
      addToast(`Please switch your account to ${currentWallet}`, { appearance: 'error', autoDismiss: true});
    else if (!fromToken?.symbol || !toToken?.symbol || !fromToken?.amount || fromToken?.amount == 0 || fromToken?.amount >= getCorrectDecValue(fromToken?.balance || 0, fromToken?.decimals, -1, false))
      addToast('Invalid token, amount', { appearance: 'error', autoDismiss: true });
    else if (library) {
      setApproveLoading(true);
      setError(false);
      const net_ret = await switchNetwork(MAIN_NETWORK_ID)
      if (net_ret) {
        const web3 = new Web3(library.provider)
        const result = await approveToken(web3, fromToken?.address, getBNValue(fromToken?.amount, fromToken?.decimals), account, CLUTCH_DEFI_ADDRESS)
        if (result.status) {
          setError(false);
          setHash(result.data.hash)
          setApproveButton(false);
        } else {
          setError(true);
          addToast('Approve failed', { appearance: 'error', autoDismiss: true });
        }
      } else {
        setError(true);
        addToast('Switch network failed', { appearance: 'error', autoDismiss: true });
      }

      setApproveLoading(false);
    }
  }

  return (
    <Box className={classes.root}>
      <Grid container spacing={2}>
        <Grid item lg={2} xs={12}>
          <StepViewer step="STEP 1:" description="Select your network" />
          <NetworkSelector
            list={networkList}
            selectedItem={selectedNetwork}
            setSelectedItem={setSelectedNetwork}
          />
        </Grid>
        <Grid item lg={6} xs={12}>
          {
            step == 0 ?
              <Card className={classes.step_card}>
                <Box className={classes.card_header}>
                  <StepViewer step="STEP 2:" description="Swap" />
                  <Box className={classes.settings_container}>
                    <Box className={classes.setting_description}>
                      Settings
                    </Box>
                    <SettingIcon className={classes.setting_icon} onClick={() => setSettingsModalOpen(true)}/>
                  </Box>
                </Box>
                <Box style={{position: "relative"}}>
                  <TokenInput tokenInfo={fromToken} isFrom={true} handleChange={(token) => handleChangeToken(token, true)} handleChangeAmount={(token) => handleChangeAmount(token)}/>
                  <RevertIcon className={classes.revertIcon} onClick={exchangeToken}/>
                  <Box style={{marginTop: '6px'}} />
                  <TokenInput tokenInfo={toToken} isFrom={false} handleChange={(token) => handleChangeToken(token, false)}/>
                </Box>
                <Box className={classes.rate_box}>
                  <Box className={classes.main}>
                    Rate:
                  </Box>
                  <Box>
                    {
                      fromToken?.symbol && toToken?.symbol 
                      ?
                      <Box className={classes.main}>
                        {`${fromToken?.amount ? 1 : 0} ${fromToken.symbol} = ${getCorrectDecValue(((toToken?.amount || 0) / (fromToken?.amount || 1)), 0, 2)} ${toToken.symbol}`}
                        <Box className={classes.sub}>
                          {`($ ${fromToken?.amount ? getCorrectDecValue(fromToken?.price || 0, 0, 2) : 0})`}
                        </Box>
                      </Box>
                      :
                      <Box className={classes.main}>
                        0
                        <Box className={classes.sub}>
                          ($ 0)
                        </Box>
                      </Box>
                    }
                  </Box>
                </Box>
                <Box className={classes.impact_box}>
                  <Box style={{display: 'flex', justifyContent: 'space-between'}}>
                    <Box className={classes.main}>
                      Expected Output
                    </Box>
                    <Box className={classes.main}>
                      {
                        toToken.symbol ?
                          `${getCorrectDecValue(Number(toToken?.amount || 0), 0, 2)} ${toToken.symbol}`
                        :
                          '-'
                      }
                    </Box>
                  </Box>
                  <Box style={{display: 'flex', justifyContent: 'space-between', marginTop: 6}}>
                    <Box className={classes.main}>
                      Price Impact
                    </Box>
                    <Box className={classes.main}>
                      {
                        (toToken?.amount || 0) * (toToken?.price || 0)  == 0 ?
                        '0%'
                        :
                        `${priceImpact}%`
                      }
                    </Box>
                  </Box>
                </Box>
                <Button 
                  variant="contained"
                  className={classes.preview_button}
                  disabled={!(fromToken?.symbol && fromToken?.amount && fromToken?.symbol != toToken?.symbol && toToken?.symbol && toToken?.amount && Number(fromToken?.amount || 0) < Number(getCorrectDecValue(fromToken?.balance || 0, fromToken?.decimals, -1, false))) || approveLoading}
                  onClick={() => {approveButton ? handleApprove() : previewSwap()}}>
                    {
                      approveButton ? 'Approve' : 'Preview Swap'
                    }
                    { !approveLoading ?
                      <PreviewIcon className={classes.preview_icon}/>
                    :
                      <CircularProgress className={classes.loading_icon}/>
                    }
                </Button>
              </Card>
            :
            step == 1 ?
              <Card className={classes.step_card}>
                <Box className={classes.back} onClick={() => setStep(0)}>
                  <BackIcon style={{marginRight: 12}}/>
                  Back
                </Box>
                <Box className={classes.title}>
                  Preview Swap
                </Box>
                <Box style={{display: 'flex', alignItems: 'center', justifyContent: 'space-between', marginTop: 18}}>
                  <Box className={classes.sub}>Network Fee:</Box>
                  <Box className={classes.info}>{`${getCorrectDecValue(gas * gasLimit, 18, 5)} ($${getCorrectDecValue(gas * gasLimit * ethPrice, 18, 2)})`}</Box>
                </Box>
                <Box style={{display: 'flex', alignItems: 'center', justifyContent: 'space-between', marginTop: 10}}>
                  <Box className={classes.sub}>Slippage tolerance:</Box>
                  <Box className={classes.info}>{inputedSlippage != '' && !isNaN(inputedSlippage) ? inputedSlippage : slippage == 0 ? 0.5 : slippage == 1 ? 1 : 2}%</Box>
                </Box>
                <Box style={{display: 'flex', alignItems: 'center', justifyContent: 'space-between', marginTop: 10}}>
                  <Box className={classes.sub}>Transaction speed:</Box>
                  <Box className={classes.info}>{speed == 0 ? 'Slow' : speed == 1 ? 'Fast' : 'Fastest'}</Box>
                </Box>
                <Box className={classes.divider}></Box>
                <Box style={{display: 'flex', alignItems: 'center', justifyContent: 'space-between', marginTop: 10}}>
                  <Box className={classes.total}>Total:</Box>
                  <Box className={classes.info}>{fromToken?.symbol == 'ETH' ? `${getCorrectDecValue(Number(fromToken?.amount) + Number(getCorrectDecValue(gas * gasLimit, 18, 5, false)), 0, 4)} ETH` : `${fromToken?.amount} ${fromToken?.symbol} + ${getCorrectDecValue(gas * gasLimit, 18, 5)} ETH`}</Box>
                </Box>
                <Button 
                  variant="contained"
                  className={classes.preview_button}
                  onClick={() => swap()}>
                    Swap Now
                </Button>
              </Card>
            : loading ?
              <Card className={classes.step_card}>
                <Box className={classes.back} onClick={() => setStep(1)}>
                  <BackIcon style={{marginRight: 12}}/>
                  Back
                </Box>
                <Box className={classes.error}>
                  Please Wait!
                </Box>
                <Box className={classes.success_description} style={{marginBottom: 100}}>
                  {`You're swaping ${fromToken.amount} ${fromToken.symbol} to ${toToken.amount} ${toToken.symbol}`}
                </Box>
              </Card>
            :
            error ?
              <Card className={classes.step_card}>
                <Box className={classes.back} onClick={() => setStep(1)}>
                  <BackIcon style={{marginRight: 12}}/>
                  Back
                </Box>
                <Box className={classes.error}>
                  Ouch!
                </Box>
                <Box className={classes.description}>
                  Something went wrong..
                </Box>
                <Button 
                  variant="contained"
                  className={classes.preview_button}
                  onClick={() => swap()}>
                    Retry
                </Button>
              </Card>
            :
              <Card className={classes.step_card}>
                <Box style={{textAlign: 'center'}}>
                  <img src="/images/Logo.svg" className={classes.logo} />
                </Box>
                <Box className={classes.error}>
                  Success!
                </Box>
                <Box className={classes.success_description}>
                  {`You just swap ${fromToken.amount} ${fromToken.symbol} to ${toToken.amount} ${toToken.symbol}`}
                </Box>
                <Box className={classes.success_description}>
                  The transaction usually takes a few seconds
                </Box>
                <Button 
                  variant="contained"
                  className={classes.sendmore_button}
                  onClick={() => setStep(0)}>
                    Swap More
                </Button>
                <Box style={{textAlign: 'center'}}>
                  <a href={`${NETWORK_TX_ENDPOINT[MAIN_NETWORK_ID]}${hash}`} target="_blank" rel="noopener noreferrer">
                    <Button size="medium" className={classes.cancel} >
                      View Transaction
                    </Button>
                  </a>
                </Box>
              </Card>
          }
          
        </Grid>
        <Grid item lg={4} xs={12}>
          <SwapChart fromToken={fromToken} toToken={toToken}/>
        </Grid>
      </Grid>
      <TransactionSettingsModal 
        open={settingsModalOpen}
        handleClose={() => setSettingsModalOpen(false)}
        slippage={slippage}
        speed={speed}
        inputedSlippage={inputedSlippage}
        handleApply={handleApply}
      />
    </Box>
  );
};

export default Swap;
